import { Header } from "./modules/header/Header";
import { MenuSidebar } from "./modules/sidebar/MenuSidebar";
import { Redirect, Route, Switch } from "react-router";
import { Preview } from "./modules/preview/Preview";
import { Constructor } from "./modules/constructor/Constructor";

const App = () => {
	return (
		<>
			<Header />
			<MenuSidebar />
			<Switch>
				<Route path="/" exact component={Preview} />
				<Route path="/constructor" component={Constructor} />
				<Redirect exact from="**" to={"/"} />
			</Switch>
		</>
	);
};
export default App;
