import { useEffect, useLayoutEffect, useState } from "react";
import { IBlockContent } from "../modules/block/Block";

export interface ILayout {
	i: string;
	x: number;
	y: number;
	w: number;
	h: number;
	static?: boolean;
	moved?: boolean;
	content: IBlockContent;
}

export type SizeType = "lg" | "md" | "sm";

export interface IResponsiveLayout {
	lg: ILayout[];
	md: ILayout[];
	sm: ILayout[];
}

export const useLayout = () => {
	const [layout, _setLayout] = useState<IResponsiveLayout>({
		lg: [],
		md: [],
		sm: [],
	});
	const [screenWidth, setScreenWidth] = useState(0);

	const setLayout = (layout: ILayout[], size?: SizeType) => {
		const actualSize = size || breakpoint;
		_setLayout((prev) => ({
			...prev,
			[actualSize]: layout,
		}));
	};

	useEffect(() => {
		console.log("layout", layout);
	}, [layout]);

	const setAllLayouts = (config: IResponsiveLayout) => {
		_setLayout(config);
	};

	const [breakpoint, setBreakpoint] = useState<SizeType>("lg");

	useLayoutEffect(() => {
		function updateSize() {
			if (window.innerWidth >= 1200) {
				setBreakpoint("lg");
			} else if (window.innerWidth >= 991 && window.innerWidth < 1200) {
				setBreakpoint("md");
			} else if (window.innerWidth >= 768 && window.innerWidth < 991) {
				setBreakpoint("sm");
			}
			setScreenWidth(window.innerWidth);
		}
		window.addEventListener("resize", updateSize);
		updateSize();
		setScreenWidth(window.innerWidth);
		return () => window.removeEventListener("resize", updateSize);
	}, []);

	const [currentElem, _setCurrentElem] = useState<ILayout | null>(null);

	const setCurrentElem = (item: ILayout | null) => {
		_setCurrentElem(item);
	};

	const [constructorRes, _setConstructorRes] = useState<SizeType>("lg");

	const setConstructorRes = (size: SizeType) => {
		_setConstructorRes(size);
	};

	const [constructorWidth, _setConstructorWidth] = useState<number>(1200);

	const setConstructorWidth = (width: number) => {
		_setConstructorWidth(width);
	};

	return {
		layout,
		currentElem,
		setCurrentElem,
		setLayout,
		breakpoint,
		screenWidth,
		constructorRes,
		setConstructorRes,
		constructorWidth,
		setConstructorWidth,
		setAllLayouts,
	};
};
