import { Sidebar } from "primereact/sidebar";
import { Button } from "primereact/button";
import { useContext } from "react";
import ModalsContext from "../../context/ModalsContext";
import { useHistory } from "react-router";

export const MenuSidebar = () => {
	const { menuSidebar, setMenuSidebar } = useContext(ModalsContext);
	const history = useHistory();

	return (
		<Sidebar
			visible={menuSidebar}
			position="left"
			className="ui-sidebar-sm"
			onHide={() => setMenuSidebar(false)}
		>
			<h1 style={{ fontWeight: "normal" }}>Sidebar menu</h1>
			<Button
				label="Preview"
				icon="pi pi-desktop"
				className="p-button-outlined"
				style={{ marginRight: ".25em" }}
				onClick={() => {
					history.push("/");
					setMenuSidebar(false);
				}}
			/>
			<Button
				icon="pi pi-cog"
				label="Constructor"
				className="p-button-outlined"
				onClick={() => {
					history.push("/constructor");
					setMenuSidebar(false);
				}}
			/>
		</Sidebar>
	);
};
