import { Button } from "primereact/button";
import { ColorPicker } from "primereact/colorpicker";
import { Dialog } from "primereact/dialog";
import { InputNumber } from "primereact/inputnumber";
import { InputText } from "primereact/inputtext";
import { useContext, useState } from "react";
import { v4 } from "uuid";
import LayoutContext from "../../context/LayoutContext";
import ModalsContext from "../../context/ModalsContext";
import { ILayout } from "../../hooks/useLayout";
import { ContolledInput } from "../controls/Controls";

export const AddBlockModal = () => {
	const { addBlock, setAddBlock } = useContext(ModalsContext);
	const { layout, setAllLayouts } = useContext(LayoutContext);
	const [inputState, setInputState] = useState({
		title: "",
		text: "",
	});

	const [colorState, setColorState] = useState({
		fontColor: "000",
		backgroundColor: "fff",
	});

	const [widthState, setWidthState] = useState(2);

	const resetToDefaultValues = () => {
		setInputState({
			text: "",
			title: "",
		});
		setColorState({
			fontColor: "000",
			backgroundColor: "fff",
		});
		setWidthState(2);
	};

	const addNewBlock = () => {
		if (inputState.title.length === 0 || inputState.text.length === 0) {
			return;
		}

		const newBlock: ILayout = {
			i: v4(),
			h: 2,
			w: widthState,
			x: 0,
			y: 0,
			content: {
				backgroundColor: colorState.backgroundColor,
				fontColor: colorState.fontColor,
				text: inputState.text,
				title: inputState.title,
			},
		};
		console.log("newBlock", newBlock);
		setAllLayouts({
			lg: [...layout["lg"], newBlock],
			md: [...layout["md"], newBlock],
			sm: [...layout["sm"], newBlock],
		});

		resetToDefaultValues();
	};
	return (
		<Dialog
			header="Add new block"
			visible={addBlock}
			footer={() => (
				<div>
					<Button
						label="Close"
						className="p-button-rounded p-button-danger"
						onClick={() => setAddBlock(false)}
					/>
					<Button
						label="Add block"
						onClick={addNewBlock}
						className="p-button-rounded p-button-success"
					/>
				</div>
			)}
			style={{ width: "50vw" }}
			onHide={() => setAddBlock(false)}
			closeOnEscape
			dismissableMask
		>
			<div className="p-fluid p-formgrid p-grid p-mt-3">
				<div className="p-field p-col-12">
					<label htmlFor="add-title">Title</label>
					<InputText
						id="add-title"
						type="text"
						value={inputState.title}
						onChange={(e: ContolledInput) => {
							setInputState((prev) => ({
								...prev,
								title: e.target.value,
							}));
						}}
					/>
				</div>
				<div className="p-field p-col-12">
					<label htmlFor="add-text">Text</label>
					<InputText
						id="add-text"
						type="text"
						value={inputState.text}
						onChange={(e: ContolledInput) => {
							setInputState((prev) => ({
								...prev,
								text: e.target.value,
							}));
						}}
					/>
				</div>
				<div className="p-field p-col-6">
					<label htmlFor="font-color">Font color</label>
					<ColorPicker
						id="font-color"
						value={colorState.fontColor}
						onChange={(e: any) => {
							setColorState((prev) => ({
								...prev,
								fontColor: e.value,
							}));
						}}
					/>
				</div>
				<div className="p-field p-col-6">
					<label htmlFor="add-bg-color">Bg color</label>
					<ColorPicker
						id="add-bg-color"
						value={colorState.backgroundColor}
						onChange={(e: any) => {
							setColorState((prev) => ({
								...prev,
								backgroundColor: e.value,
							}));
						}}
					/>
				</div>
				<div className="p-field p-col-12">
					<label htmlFor="add-width">Width</label>

					<InputNumber
						id="add-width"
						value={widthState}
						onValueChange={(e) => {
							setWidthState(e.value);
						}}
						showButtons
						buttonLayout="horizontal"
						step={1}
						decrementButtonClassName="p-button-danger"
						incrementButtonClassName="p-button-success"
						incrementButtonIcon="pi pi-plus"
						decrementButtonIcon="pi pi-minus"
						mode="decimal"
						min={1}
						max={12}
						onBlur={() => {}}
						tooltip={
							"Screen is represented as 12-col grid. Choose how many columns your block will occupy"
						}
						tooltipOptions={{
							position: "top",
						}}
					/>
				</div>
			</div>
		</Dialog>
	);
};
