import { useContext } from "react";
import ReactGridLayout from "react-grid-layout";
import { NavLink } from "react-router-dom";
import LayoutContext from "../../context/LayoutContext";
import { Block } from "../block/Block";
import { ResolutionInfo } from "../resolution-info/ResolutionInfo";
import "./Preview.scss";

export const Preview = () => {
	const { layout, screenWidth, breakpoint } = useContext(LayoutContext);

	const getLayouts = () => {
		return layout[breakpoint];
	};
	return (
		<>
			{layout[breakpoint].length === 0 ? (
				<div className="preview">
					<h2 className="preview__title">
						Seems like there are no blocks on current resolution.
					</h2>
					<NavLink to="/constructor" className="preview__link">
						Go to block constructor
					</NavLink>
					<ResolutionInfo />
				</div>
			) : (
				<>
					<div style={{ padding: "0 20px" }}>
						<ResolutionInfo />
					</div>

					<ReactGridLayout
						layout={getLayouts()}
						width={screenWidth}
						cols={12}
						rowHeight={30}
						isDraggable={false}
						isBounded={true}
						isResizable={false}
						isDroppable={false}
					>
						{layout[breakpoint].map((block) => (
							<div
								key={block.i}
								className="scroll"
								style={{
									background: `#${block.content.backgroundColor}`,
								}}
							>
								<Block {...block.content} />
							</div>
						))}
					</ReactGridLayout>
				</>
			)}
		</>
	);
};
