import "./ControlPanel.scss";
import { TabView, TabPanel } from "primereact/tabview";
import { useContext, useState } from "react";
import { SizeType } from "../../hooks/useLayout";
import LayoutContext from "../../context/LayoutContext";
import { Controls } from "../controls/Controls";
import ModalsContext from "../../context/ModalsContext";
import { Button } from "primereact/button";

interface ITabChangeEvent {
	originalEvent: Event;
	index: number;
}

interface ISizesArray {
	breakpoint: SizeType;
	width: number;
}

export const ControlPanel = () => {
	const sizes: ISizesArray[] = [
		{
			breakpoint: "lg",
			width: 1200,
		},
		{
			breakpoint: "md",
			width: 991,
		},
		{
			breakpoint: "sm",
			width: 768,
		},
	];
	const [activeIndex, setActiveIndex] = useState(0);
	const { setConstructorRes, setConstructorWidth } = useContext(LayoutContext);
	const { setConfig, setAddBlock } = useContext(ModalsContext);
	const tabs = ["Desktop", "Tablet", "Mobile"];

	const onTabChange = (e: ITabChangeEvent) => {
		setActiveIndex(e.index);
		setConstructorRes(sizes[e.index].breakpoint);
		setConstructorWidth(sizes[e.index].width);
	};

	return (
		<div className="control">
			<div className="p-d-flex p-jc-between">
				<Button
					label="Add block"
					className="p-button-sm p-button-success"
					onClick={() => setAddBlock(true)}
				/>
				<Button
					icon="pi pi-times"
					className="p-button-rounded p-button-danger p-button-outlined p-button-sm"
					onClick={() => setConfig(false)}
				/>
			</div>

			<TabView activeIndex={activeIndex} onTabChange={onTabChange}>
				{tabs.map((tab) => (
					<TabPanel header={tab} key={tab.toLowerCase()}>
						<Controls />
					</TabPanel>
				))}
			</TabView>
		</div>
	);
};
