import { useContext } from "react";
import LayoutContext from "../../context/LayoutContext";

export const ResolutionInfo = () => {
	const { screenWidth, breakpoint } = useContext(LayoutContext);
	return (
		<span>
			Current breakpoint: <strong>{breakpoint}</strong>. Current screen
			width: <strong>{screenWidth}</strong>
		</span>
	);
};
