export interface IBlockContent {
	title: string;
	text: string;
	fontColor: string;
	backgroundColor: string;
}

export const Block: React.FC<IBlockContent> = ({
	backgroundColor,
	title,
	text,
	fontColor,
}) => {
	return (
		<div
			style={{
				backgroundColor: "inherit",
				color: `#${fontColor}`,
				height: "100%",
				width: "100%",
				overflow: "scroll",
			}}
			className="scroll"
		>
			<div>
				<strong>{title}</strong>
			</div>
			<p style={{ margin: 0 }}>{text}</p>
		</div>
	);
};
