import { useState } from "react";

export const useModals = () => {
	const [menuSidebar, _setMenuSidebar] = useState(false);
	const [config, _setConfig] = useState(true);
	const [addBlock, _setAddBlock] = useState(false);

	const setAddBlock = (state: boolean) => {
		_setAddBlock(state);
	};
	const setMenuSidebar = (state: boolean) => {
		_setMenuSidebar(state);
	};
	const setConfig = (state: boolean) => {
		_setConfig(state);
	};
	return {
		menuSidebar,
		setMenuSidebar,
		config,
		setConfig,
		addBlock,
		setAddBlock,
	};
};
