import React, { useContext, useEffect, useState } from "react";
import LayoutContext from "../../context/LayoutContext";
import { InputText } from "primereact/inputtext";
import { ColorPicker } from "primereact/colorpicker";
import { ILayout } from "../../hooks/useLayout";
import { InputNumber } from "primereact/inputnumber";

export type ContolledInput<T = HTMLInputElement> = React.ChangeEvent<T>;

export const Controls = () => {
	const { layout, setCurrentElem, currentElem, setAllLayouts } = useContext(
		LayoutContext
	);

	useEffect(() => {
		setInputState({
			title: currentElem?.content.title ?? "",
			text: currentElem?.content.text ?? "",
		});
		setColorState({
			fontColor: currentElem?.content.fontColor ?? "000",
			backgroundColor: currentElem?.content.backgroundColor ?? "fff",
		});

		setWidthState(currentElem?.w ?? 2);
	}, [currentElem]);

	const [inputState, setInputState] = useState({
		title: "",
		text: "",
	});

	const [colorState, setColorState] = useState({
		fontColor: "000",
		backgroundColor: "fff",
	});

	const [widthState, setWidthState] = useState(2);

	const widthOnChange = (value: number) => {
		if (currentElem) {
			const updatedCurrentElem: ILayout = {
				...currentElem,
				w: value,
			};
			saveChanges(updatedCurrentElem);
		}
	};

	const inputOnBlur = (input: "title" | "text") => {
		if (currentElem) {
			const updatedCurrentElem: ILayout = {
				...currentElem,
				content: {
					...currentElem.content,
					[input]: inputState[input],
				},
			};

			saveChanges(updatedCurrentElem);
		}
	};

	const colorOnChange = (
		color: "fontColor" | "backgroundColor",
		value: string
	) => {
		if (currentElem) {
			const updatedCurrentElem: ILayout = {
				...currentElem,
				content: {
					...currentElem.content,
					[color]: value,
				},
			};

			saveChanges(updatedCurrentElem);
		}
	};

	const changeContent = (layout: ILayout[], changedContent: ILayout) => {
		const operatedLayout = [...layout];
		const indexOfUpdatedElem = operatedLayout.findIndex(
			(block) => block.i === changedContent.i
		);
		indexOfUpdatedElem !== -1 &&
			operatedLayout.splice(indexOfUpdatedElem, 1, changedContent);
		console.log("changedContent", changedContent);
		return operatedLayout;
	};

	const saveChanges = (changes: ILayout) => {
		setCurrentElem(changes);
		setAllLayouts({
			lg: changeContent(layout.lg, changes),
			md: changeContent(layout.md, changes),
			sm: changeContent(layout.sm, changes),
		});
	};

	return (
		<>
			{!currentElem ? (
				<p>Seems like there is no selected block</p>
			) : (
				<div className="p-fluid p-formgrid p-grid p-mt-3">
					<div className="p-field p-col-12">
						<label htmlFor="title">Title</label>
						<InputText
							id="title"
							type="text"
							value={inputState.title}
							onChange={(e: ContolledInput) => {
								setInputState((prev) => ({
									...prev,
									title: e.target.value,
								}));
							}}
							onBlur={() => inputOnBlur("title")}
						/>
					</div>
					<div className="p-field p-col-12">
						<label htmlFor="text">Text</label>
						<InputText
							id="text"
							type="text"
							value={inputState.text}
							onChange={(e: ContolledInput) => {
								setInputState((prev) => ({
									...prev,
									text: e.target.value,
								}));
							}}
							onBlur={() => inputOnBlur("text")}
						/>
					</div>
					<div className="p-field p-col-6">
						<label htmlFor="font-color">Font color</label>
						<ColorPicker
							id="font-color"
							value={colorState.fontColor}
							onChange={(e: any) => {
								colorOnChange("fontColor", e.value);
							}}
						/>
					</div>
					<div className="p-field p-col-6">
						<label htmlFor="bg-color">Bg color</label>
						<ColorPicker
							id="bg-color"
							value={colorState.backgroundColor}
							onChange={(e: any) => {
								colorOnChange("backgroundColor", e.value);
							}}
						/>
					</div>
					<div className="p-field p-col-12">
						<label htmlFor="width">Width</label>

						<InputNumber
							id="width"
							value={widthState}
							onValueChange={(e) => {
								widthOnChange(e.value);
							}}
							showButtons
							buttonLayout="horizontal"
							step={1}
							decrementButtonClassName="p-button-danger"
							incrementButtonClassName="p-button-success"
							incrementButtonIcon="pi pi-plus"
							decrementButtonIcon="pi pi-minus"
							mode="decimal"
							min={1}
							max={12}
							onBlur={() => {}}
							tooltip={
								"Screen is represented as 12-col grid. Choose how many columns your block will occupy"
							}
							tooltipOptions={{
								position: "top",
							}}
						/>
					</div>
				</div>
			)}
		</>
	);
};
