import { Button } from "primereact/button";
import { useContext } from "react";
import ReactGridLayout from "react-grid-layout";
import LayoutContext from "../../context/LayoutContext";
import ModalsContext from "../../context/ModalsContext";
import { ILayout } from "../../hooks/useLayout";
import { AddBlockModal } from "../add-block/AddBlockModal";
import { Block } from "../block/Block";
import { ControlPanel } from "../control-panel/ControlPanel";

export const Constructor = () => {
	const {
		layout,
		constructorRes,
		constructorWidth,
		setLayout,
		setCurrentElem,
		currentElem,
	} = useContext(LayoutContext);

	const { config, setConfig } = useContext(ModalsContext);

	const getConfiguredLayouts = () => {
		return layout[constructorRes];
	};

	const onLayoutChange = (newLayout: ReactGridLayout.Layout[]) => {
		console.log("newLayout", newLayout);
		const layoutCopy: ILayout[] = layout[constructorRes].map((block) => {
			const changedBlock = newLayout.find(
				(newBlock) => newBlock.i === block.i
			);
			// I'm too tired to deal with shit like possible changedBlock undefined status
			return {
				i: changedBlock!.i,
				content: block.content,
				x: changedBlock!.x,
				y: changedBlock!.y,
				w: changedBlock!.w,
				h: changedBlock!.h,
			};
		});

		setLayout(layoutCopy, constructorRes);
	};

	return (
		<>
			<AddBlockModal />
			{config ? (
				<ControlPanel />
			) : (
				<div className="controls-button">
					<Button
						label="Show controls"
						className="p-button-rounded p-button-warning"
						onClick={() => setConfig(true)}
					/>
				</div>
			)}
			<div className="constructor">
				<span>
					<strong>{constructorWidth}px</strong>
				</span>
				{
					<div
						className={`constructor__grid`}
						style={{ width: `${constructorWidth}px` }}
					>
						<ReactGridLayout
							layout={getConfiguredLayouts()}
							width={constructorWidth}
							cols={12}
							rowHeight={30}
							isBounded={true}
							onLayoutChange={onLayoutChange}
							resizeHandles={["sw", "nw", "se", "ne"]}
						>
							{layout[constructorRes].map((block) => (
								<div
									key={block.i}
									onClick={() => setCurrentElem(block)}
									style={{
										cursor: "pointer",
										boxShadow:
											block.i === currentElem?.i
												? "0 0 15px rgba(255,69,0,0.6)"
												: "none",
										background: `#${block.content.backgroundColor}`,
										padding: "0 20px",
									}}
								>
									<Block {...block.content} />
								</div>
							))}
						</ReactGridLayout>
					</div>
				}
			</div>
		</>
	);
};
