import { createContext } from "react";
import { useModals } from "../hooks/useModals";

const ModalsContext = createContext<ReturnType<typeof useModals>>({
	menuSidebar: false,
	setMenuSidebar: () => {},
	config: true,
	setConfig: () => {},
	setAddBlock: () => {},
	addBlock: false,
});

export const ModalsState: React.FC = ({ children }) => {
	const context = useModals();

	return (
		<ModalsContext.Provider value={context}>
			{children}
		</ModalsContext.Provider>
	);
};

export default ModalsContext;
