import { createContext } from "react";
import { ILayout, IResponsiveLayout, useLayout } from "../hooks/useLayout";

const LayoutContext = createContext<ReturnType<typeof useLayout>>({
	currentElem: {} as ILayout,
	layout: {} as IResponsiveLayout,
	setCurrentElem: () => {},
	setLayout: () => {},
	breakpoint: "lg",
	screenWidth: 0,
	constructorRes: "lg",
	setConstructorRes: () => {},
	constructorWidth: 0,
	setConstructorWidth: () => {},
	setAllLayouts: () => {},
});

export const LayoutState: React.FC = ({ children }) => {
	const context = useLayout();
	return (
		<LayoutContext.Provider value={context}>
			{children}
		</LayoutContext.Provider>
	);
};

export default LayoutContext;
